* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.app {
    overflow: hidden;
}

.container-panel {
    min-height: calc(100vh - 131px);
}

.header {
    padding: 5px 0;
}

.image {
    min-height: 50px;
    min-width: 50px;
    height: 10vh;
    width: 10vh;
}

.btn {
    margin: 5px 0;
}

.size-letter {
    text-transform: capitalize;
}

.spinner {
    position: fixed;
    top: 35%;
    left: 49%;
}

.header-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.alert-deleted-user {
    text-align: center;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    background-color: #F8D7DA;
    color: #8F444B;
    padding: 7px 3px;
    border-radius: 5px;
    margin-left: 10px;
}

@media(orientation:portrait) {
    .image {
        max-height: 50px;
        max-width: 50px;
    }
}
